.image {
    height: 300px;
    width: 400px;
    border-radius: 10px;
}

.feature {
    position: relative;
    bottom: 5%;
}

.full {
    background-image: linear-gradient(to bottom, #ffffff, #edfaff, #ccf9ff, #b6f7e8, #c4efbb);
    position: relative;
}

.quote {
    width: 30%;
    position: absolute;
    top: 85%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
}

.quote-img{
    filter: brightness(80%);
    border-radius: 20px;
}